.about{margin-bottom:100px;height:auto}.about__inner{display:grid;grid-template-columns:repeat(2, 1fr)}@media(max-width: 780px){.about__inner{grid-template-columns:repeat(1, 1fr)}}.about__img-inner{max-height:700px;max-width:640px;margin-right:60px}@media(max-width: 780px){.about__img-inner{margin-right:0;margin-bottom:30px;max-width:100%}}.about__img{width:100%;height:100%;object-fit:cover}.about__content{display:flex;flex-direction:column;align-items:flex-start;justify-content:center}.about__items{width:100%;display:grid;grid-template-columns:repeat(2, 1fr);grid-gap:100px 40px}@media(max-width: 430px){.about__items{grid-gap:40px}}.about__item{max-width:60%;width:100%;margin:0 auto}@media(max-width: 1480px){.about__item{max-width:100%}}@media(max-width: 780px){.about__item{display:flex;flex-direction:column;align-items:center}}.about__decor{font-family:"PoiretOne";font-size:100px;color:#8f4e30;display:block;position:relative;letter-spacing:5px}@media(max-width: 900px){.about__decor{font-size:70px}}@media(max-width: 780px){.about__decor{font-size:100px;display:inline-block}}@media(max-width: 430px){.about__decor{font-size:64px}}.about__decor::before{content:"→";position:absolute;font-size:48px;bottom:15px;left:-15px}@media(max-width: 430px){.about__decor::before{bottom:0;font-size:42px}}.about__text{font-family:"Raleway";font-style:normal;font-weight:300;font-size:16px;letter-spacing:1px;color:#000;padding-left:40px;text-align:center;max-width:240px}@media(max-width: 900px){.about__text{padding-left:0}}@media(max-width: 430px){.about__text{font-size:12px}}